.suptitle__left {
	display: inline-block;
	position: relative;
	padding-left: 104px;
	font-weight: 400;
	font-size: 22px;
	line-height: 22px;
	letter-spacing: 7.3px;
	opacity: 0.5;
	text-transform: uppercase;
	&::before {
		content: "";
		position: absolute;
		left: 0;
		top: 50%;
		width: 80px;
		height: 1px;
		background-color: #fff;
	}
}
