.header {
	position: relative;
	height: 100vh;
}
// .header__slider .header__slider.header__slider.header__slider.header__slider.header__slider
.header__slider {
	&-item {
		position: relative;
		height: 100vh;
		background-image: url(../img/bg-header.jpg);
		background-repeat: no-repeat;
		background-size: cover;
		background-position: center;
	}
}

.slider-item__info {
	position: absolute;
	right: 80px;
	bottom: 116px;
	color: #ffffff;
	text-transform: uppercase;
	text-align: right;
	&-title {
		margin-bottom: 48px;
		font-size: 80px;
		font-weight: 800;
		line-height: 86px;
	}
	&-text {
		font-weight: 800;
		font-size: 40px;
		line-height: 40px;
		margin-bottom: 20px;
	}
	&-suptitle {
		margin-bottom: 5px;
	}
	&-suptext {
		margin-bottom: 8px;
	}
	&-link {
		display: inline-block;
	}
}

.slider-arrows {
	position: absolute;
	z-index: 2;
	top: 40px;
	right: 80px;
	cursor: pointer;
}

.slider-arrows__left {
	right: 125px;
}
// .header__slider.header__slider.header__slider.header__slider.header__slider.header__slider

// .slider-dots .slider-dots .slider-dots .slider-dots .slider-dots .slider-dots .slider-dots
.slider-dots {
	// height: 34px;
	position: absolute;
	right: 50px;
	bottom: 40px;
	max-width: 920px;
	width: 100%;
	color: #fff;
	.slick-current {
		.dots-box {
			border-top: 4px solid $green;
		}
	}
}



.dots-box {
	width: 200px;
	height: 34px;
	padding-top: 6px;
	border-top: 4px solid #fff;
	display: flex;
	justify-content: space-between;
	align-items: center;
	&__number {
		font-weight: 800;
		font-size: 24px;
		line-height: 34px;
		text-transform: uppercase;
	}
	&__name {
		font-weight: 300;
		font-size: 16px;
		line-height: 19px;
	}
}
